const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list: []
		};
	},
	onLoad() {
		// 获取收藏列表
		this.getList()
	},
	methods:{
		getList() {
			// 获取收藏列表
			uni.showLoading({
			  title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/collections`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == '1') {
						this.list = res.data.data
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
			
				}
			})
		}
	}
}
